.contacts-item {
    padding   : 8px 4px;
    margin    : 0px 12px;
    cursor    : pointer;
    border-top: 1px solid #e8e8e8;

    display    : flex;
    align-items: center;
}

.contacts-item:nth-child(2) {
    border: none;
}

.contacts-item-content {
    flex-grow: 1;
    padding: 10px;
    color    : #7b7b7b;
    display: flex;    
    flex-direction: column;

}


.contacts-item:hover .contacts-item-content {
    color: #4e4e4e;
}

#name {
    font-weight: bold;
    font-size: 15px;
}

#number {
    font-size: 14px;
}
.contacts-first-letter {
    width         : 25px;
    height        : 25px;

    text-align    : center;
    font-weight   : bold;
    background    : #ecb2c3;
    padding       : 8px 10px;
    border-radius : 100%;
    color         : white;
    font-size     : 22px;
}


.delete-btn {
    display            : inline-block;
    width              : 22px;
    height             : 22px;
    background-image   : url(../../images/icon-delete-light.png);
    background-size    : contain;
    background-repeat  : no-repeat;
    background-position: center;

    flex-shrink: 0;
}

.delete-btn:hover {
    background-image: url(../../images/icon-delete.png);
}

