footer {
    display                   : flex;
    padding                   : 4px 4px;
    align-items               : center;
    flex-shrink               : 0;
    border-bottom-left-radius : 10px;
    border-bottom-right-radius: 10px;
    background-color: #f5f5f5;
}

.content {    
    flex-grow: 1;    
}

.input-icons { 
    display: flex;
} 

.input-field {
    border : none;
    padding: 8px;
    background: transparent;    
    box-sizing: border-box;
    flex-grow: 1;
}

.input-field:focus,
.input-field:active,
.input-field:hover {
    border : none;
    outline: none;
}

.icon { 
    padding-left: 12px; 
    width: 30px;
    height: 30px; 
    display: flex;
} 

.icon::before{
    align-self: center;
    color: grey;
}

.warning .icon::before{
    color: red;
}

#add-btn {
    padding      : 12px;
    font-weight  : bold;
    cursor       : pointer;
    color        : grey;
}

#add-btn:hover {
    transform: translateY(-3px);
    background-color: white;
    box-shadow: 3px 3px 3px #d6d6d6;;
}


